
import { defineComponent, PropType } from "vue";
import { Product } from "../data/entities";

export default defineComponent({
  name: "ProductItem",

  props: {
    product: {
      type: Object as PropType<Product>,
    },
  },

  data() {
    return {
      quantity: 1,
    };
  },

  methods: {
    handleAddToCart() {
      this.$emit("addToCart", {
        product: this.product,
        quantity: this.quantity,
      });
    },
  },
});
