
import { defineComponent } from "vue";
import { useStore } from "vuex";

export default defineComponent({
  name: "Header",

  setup() {
    return { store: useStore() };
  },

  computed: {
    displayText(): string {
      const count = this.store.state.order.productCount;
      return count === 0
        ? "(No Selection)"
        : `${count} product(s), ` +
            `$${this.store.state.order.total.toFixed(2)}`;
    },
  },
});
